<template>
  <!---->
  <el-dialog
      title="()"
      width="50%"
      :visible.sync="addDialogVisible">
    <el-form :model="questionForm"
             :rules="questionFormRules"
             ref="questionFormRef"
             label-width="80px"
    >
      <el-form-item label="" prop="title">
        <el-input placeholder=""
                  maxlength="30"
                  show-word-limit
                  v-model="questionForm.title" :autosize="true"
                  type="textarea" :clearable="true" resize="none"> </el-input>
      </el-form-item>
      <el-form-item label="" prop="content">
        <el-input placeholder=""
                  maxlength="60"
                  show-word-limit
                  v-model="questionForm.content" :autosize="true"
                  type="textarea" :clearable="true" resize="none"> </el-input>
      </el-form-item>
      <el-form-item label="" prop="ismust">
        <el-switch v-model="questionForm.ismust"
                   active-color="#409eff"
                   inactive-color="#dcdfe6"></el-switch>
      </el-form-item>

      <el-form-item
          v-for="(choice, index) in questionForm.groups"
          :label="' ' + (index+1) "
          :key="choice.key"
          :prop="'groups.' + index + '.content'"
          :rules="{
                    required: true,  message: '', trigger: 'blur'
                }"
      >
        <el-input v-model="choice.content" class="choiceinput">
        </el-input >
        <el-button @click.prevent="removeChoice(choice)" type="danger"></el-button>
      </el-form-item>
    </el-form>
    <div class="dialog-footer">
      <el-button icon="el-icon-edit" @click="addChoice" type="primary"></el-button>
      <div>
        <el-button icon="el-icon-check" @click="finishQuestion" type="success"></el-button>
        <el-button icon="el-icon-close" @click="cancelQuestion" type="danger"> </el-button>
      </div>
    </div>
  </el-dialog>
</template>
<script>

export default {
  name: "multiple-completion-addcard",
  data(){
    return{
      addDialogVisible : false,
      questionForm: {
        content: '',
        ismust: false,
        groups: [
        ],
        answer: ''
      },
      questionFormRules:{
        content:[
          {
            required: true,
            message: '',
            tragger: 'blur'
          }
        ]
      }
    }
  },
  methods:{
    resetForm(questionType){
      this.questionForm = {
        key: Date.now(),
        type: questionType,
        content: '',
        ismust: false,
        groups: [
        ],
        answer: []
      }
    },
    addQuestion(questionType){
      this.resetForm(questionType)
      this.addDialogVisible = true
    },
    editQuestion(question){
      console.log(question)
      this.questionForm = question
      this.addDialogVisible = true
    },
    addChoice() {
      this.questionForm.groups.push({
        content: '',
        key: Date.now()})
      this.questionForm.answer.push('')
    },
    removeChoice(item) {
      var index = this.questionForm.groups.indexOf(item)
      if (index !== -1) {
        this.questionForm.groups.splice(index, 1)
      }
    },
    finishQuestion(){
      this.$refs.questionFormRef.validate(valid => {
        if (!valid) return this.$notify.error("");
        if (this.questionForm.groups.length === 0) return this.$notify.error("")
        this.addDialogVisible = false
        var index = this.$parent.questionnair.questions.indexOf(this.questionForm)
        if (index === -1){
          this.$parent.questionnair.questions.push(this.questionForm)
        }
        else{
          this.$parent.questionnair.questions[index] = this.questionForm
          this.$parent.$forceUpdate()
        }
      })
    },
    cancelQuestion(){
      console.log(111)
      this.$refs.questionFormRef.resetFields()
      this.addDialogVisible = false
    }
  }
}
</script>
<style lang="less" scoped>
.el-button{
  color: #fff;
}
.choiceinput{
  width: 70%;
  margin-right: 10%;
}
.dialog-footer{
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0;
}
</style>
